:root {
  --gray: #767272;
  --light-gray: #DDDADA;
  --basic-gray: #E0E0E0;
  --dark-gray: #585050;
  --black: #000000;
  --green: #1BC943;
  --white: #ffffff;
  --blue: #0E49B5;
  --dark-blue: #3B3E66;
  --red: #F05454; }

*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", serif; }

ul[class],
ol[class] {
  list-style: none; }

input,
button,
textarea,
select {
  font: inherit; }

img {
  max-width: 100%;
  display: block; }

body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li {
  margin: 0; }

.container {
  max-width: 1028px;
  margin: 0 auto;
  padding: 0 10px; }

.icon {
  font-size: 20px; }

.add-to-cart {
  background-color: var(--blue);
