  transition: .3s background-color ease-in-out, .3s color ease-in-out, .3s border ease-in-out;
  cursor: pointer;
  border: 1px var(--blue) solid;
  border-radius: 5px; }
  .add-to-cart:hover {
    background-color: var(--white);
    color: var(--blue);
    border: 1px var(--blue) solid; }

[disabled] {
  background-color: var(--light-gray);
  border: 1px var(--light-gray) solid;
  cursor: auto; }
  [disabled]:hover {
    background-color: var(--light-gray);
    color: var(--white);
    border: 1px var(--light-gray) solid; }

.top-header {
