      cursor: pointer; }
      .search-block .wrap-settings .item-setting:hover .icon-filter,
      .search-block .wrap-settings .item-setting:hover .icon-settings,
      .search-block .wrap-settings .item-setting:active .icon-filter,
      .search-block .wrap-settings .item-setting:active .icon-settings {
