@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon/icomoon.eot?rk1x1l');
  src:  url('../fonts/icomoon/icomoon.eot?rk1x1l#iefix') format('embedded-opentype'),
  url('../fonts/icomoon/icomoon.ttf?rk1x1l') format('truetype'),
  url('../fonts/icomoon/icomoon.woff?rk1x1l') format('woff'),
  url('../fonts/icomoon/icomoon.svg?rk1x1l#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

